* {
  box-sizing: border-box;
}

body {
  padding: 0em;
  margin: .5em;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
  padding: 0;
  margin: 0; 
}

h1 {
  font-family: 'Playfair Display', serif;
  font-size: 28px;
  font-weight: bold;
  letter-spacing: .5px;
  margin: 0;
}

h2,h3,h4,h5 {
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: bold;
  margin: 0;
}

h3 {
  font-size: 22px;
  letter-spacing: .6px;
}

h4 {
  font-size: 18px;
  letter-spacing: .5px;
}

h5 {
  font-size: 15px;
  letter-spacing: .4px;
}

a {
  text-decoration: none;
  color: #000000;
}

.logos {
  display: flex;
  justify-content: space-between;
}

.shellicon, .hearticon {
  padding: .5em 0em;
}

.search {
  margin-top: 20px;
}

/* For search.js component's styling */
.search-section {
  padding: 25px;
  background-color: #6BE0CB;
  border-radius: 7px;
  display: flex;
  flex-direction: column;
  margin-top: 25px;
}

.search-top {
  display: flex;
  justify-content: left;
  margin: 0;
  margin-bottom: 10px;
  padding: 0;
}

.search-icon img {
  width: 18px;
  margin-right: 7px;
}

.search-podcasts {
  margin: 0;
  padding: 0;
}

input {
  height: 30px;
  width: 100%;
  border-radius: 7px;
  background-color: #f5f5f5;
  border: .1px solid #f5f5f5;
  position: relative;
}

input:focus {
  outline: none;
}

.search-keyword-form {
  position: relative;
}

.search-keyword-form.forwardarrow {
  position: absolute;
  top: .5em;
  right: .7em;
}

