/* For results.js component's styling */
.loadingShell {
  display: grid;
  margin: auto 0;
  padding: 2em;
  justify-content: center;
  align-items: center;
}

.results-control {
  display: flex;
  justify-content: left;
  margin: 25px 0px;
}

.results-backarrow img {
  margin: 0;
  height: 18px;
  margin-top: 12px;
  margin-right: 10px;
}

.results-header {
  margin: 0;
}

/* For player.js component's styling */
.podcast-list {
  display: grid;
  width: 100%;
}

.podcast {
  display: flex;
  margin-bottom: 15px;
}

.podcast-left img {
  width: 75px;
  margin-right: 10px;
  border-radius: 7px;
}

.podcast-header {
  font-size: 15px;
  font-weight: bold;
  letter-spacing: .4px;
  margin: 0;
}

p {
  margin: 0;
  font-size: 14px;
  letter-spacing: .4px;
}

p.podcast-desc {
  height: 3em;
}

.nopodcast {
  color: #6BE0CB;
}

span {
  color: #4045F5;
}

.episode-favicon {
  justify-content: flex-end;
  direction: rtl;
}

.episode-img img {
  border-radius: 12px;
  width: 150px;
  margin-right: 10px;
}

.episode-detail {
  display: flex;
  flex-direction: row;
  margin-bottom: 15px;
}

.episode-desc{
  display: flex;
  flex-direction: column;  
  margin-top: 10px;
  max-height: 150px;
  overflow: hidden;
} 

.episode-desc p {
  margin-top: 2px;
  height: 5em;
  overflow: hidden;
}

.audio-control{
  width: 100%;
}

.audio-player {
  background-color: #6BE0CB;
  border: none;
  padding: 0;
}

/* All rhap_* for AudioPlayer component's overrides */
.rhap_container, .rhap_container *:focus {
  box-shadow: none;
  border: none;
  outline: none;
}

.rhap_progress-section {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  font-family: 'Source Sans Pro', sans-serif;
}

.rhap_progress-container {
  grid-area: 1/1/2/4;
  margin: 0px;
}
#rhap_current-time {
  grid-area: 2/1/2/2;
  font-size: 14px;
  letter-spacing: .4px;
}

.rhap_total-time {
  grid-area: 2/3/3/4;
  font-size: 14px;
  letter-spacing: .4px;
  text-align: right;
}

.rhap_progress-bar {
  height: 10px;
  background-color: #ffffff;
  outline: none;
  border-radius: none;
}

.rhap_progress-filled {
  background-color: #4045F5;
  border-radius: 0px;
}

.rhap_download-progress {
  display: none;
}

.rhap_progress-indicator {
  display: none;
}

.rhap_controls-section .rhap_additional-controls {
  flex: 0;
}

.rhap_controls-section .rhap_main-controls {
  flex: 3;
  flex-direction: row;
  justify-content: center;
}

.rhap_main-controls-button {
  color: #f5f5f5;
}

.rhap_repeat-button {
  display: none;
}

.rhap_play-pause-button {
  margin: 0 .5em;
}

.rhap_play-pause-button svg{
  position: relative;
  color: #4045F5;
  background-color: #ffffff;
  border-radius: 50%;
  width: 100%;
} 

.rhap_volume-container {
  margin-right: 7px;
}

.rhap_volume-button {
  color: #f5f5f5;
}

.rhap_volume-bar {
  background: #ffffff;
  border-radius: 2px;
}

.rhap_volume-indicator {
  background: #4045F5;
}

#play-1, #play-2 {
  background-color: #6BE0CB;
  border-radius: 50%;
  border: none;
  outline: none;
}

.episode-playing {
  background-color: #6BE0CB;
  display: flex;
  flex-direction: column;
  padding: 1em 1em 1.2em;
  margin: 1em 0em;
  border-radius: .5em;
}

.nextEpisode {
  margin: 30px 0px 10px;
}

.nextEpisode-list {
  display: grid;
  margin: 10px 0px;
}

.nextEpisode-list-item {
  background-color: #CDF4ED;
  padding: 12px;
  margin: 7px 2px;
  display: flex;
  border-radius: 10px;
  border-left: 10px solid #6BE0CB;
}

.nextEpisode-list-item-left {
  flex: 80%;
}

.nextEpisode-list-item-right {
  display: grid;
  place-content: center;
} 

button#play-2 {
  padding: auto 0px;
}

#play-2 {
  background-color: #CDF4ED;
}


@media all and (min-width: 480px) {

  .App {
    justify-content: center;
    margin: 0px 3em;
  }

}

@media all and (min-width: 768px) {

  .App {
    justify-content: center;
    margin: 0px 6em;
  }

}
